    setHeight = () => {
        // console.log(this.gridRefs);
    // console.log(this.state);
        this.pageRefs.slice(0,this.state.numberOfPages).map(item=>{
            if(item && item.current && item.current.offsetWidth) {
                item.current.style.height = item.current.offsetWidth * 1.4 + 'px';
                item.current.style.maxHeight = item.current.offsetWidth * 1.4 + 'px';
                item.current.style.fontSize = (item.current.offsetWidth * 1.4) * 0.021 + 'px';
            }
        });

        this.forceUpdate()

    };

    getWidth = () =>{
      this.setState({
          ...this.state,
          width: this.pageRef1.current.offsetWidth * 1.4 + 'px',
          fontSize:(this.pageRef1.current.offsetWidth * 1.4)*0.02  + 'px'
      })
    };

    componentWillMount() {

    }

    componentDidMount() {
        this.setHeight();
        window.addEventListener("resize", this.setHeight);
    }

    componentWillUnmount() {
        window.removeEventListener("resize", this.setHeight);
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
       return nextProps.shouldUpdate!==null&&nextProps.shouldUpdate !== this.state.shouldUpdate
    }
    
    componentWillUpdate(nextProps, nextState, nextContext) {

            this.setState({
                ...this.state,
                shouldUpdate:this.props.shouldUpdate
            });

        let state = {...this.state};
        let gridRefs = [...this.gridRefs];
        let pageRefs = [...this.pageRefs];
        let {pages} = state;
        let {numberOfPages} = state;

        let {cvStyle} = nextProps.data;
        let style = state.style;
        if (cvStyle !== style && cvStyle !== '') {
            this.setState({
                ...this.state,
                style:cvStyle
            })
        }else if (cvStyle === '' && style!=="default"){
            this.setState({
                ...this.state,
                style:'default'
            })
        }

        if (nextProps.width !== this.state.width){
            this.setState({
                ...this.state,
                width:nextProps.width
            },this.setHeight)
        }




        pages.slice(0,numberOfPages).map((e,i)=> {
            if (gridRefs[i] && gridRefs[i].current && gridRefs[i].current.offsetHeight * 1.01 > pageRefs[i].current.offsetHeight
                && numberOfPages !== 4
                && numberOfPages === nextState.numberOfPages
                && pages.every((item,index)=>item.itemsOnPage===nextState.pages[index].itemsOnPage)){

                // console.log('1111111');
                pages[i].itemsOnPage--;
                pages[i + 1].itemsOnPage++;

                this.setState({
                    ...this.state,
                    pages: pages,
                    numberOfPages: numberOfPages <= i + 1 ? numberOfPages + 1 : numberOfPages
                });

            }
            else
            if (i>=1 && gridRefs[i-1].current.offsetHeight * 1.01 < pageRefs[i-1].current.offsetHeight
                && gridRefs[i].current && numberOfPages === nextState.numberOfPages
                && pages.every((item,index)=>item.itemsOnPage===nextState.pages[index].itemsOnPage)){
                    if ((gridRefs[i-1].current.offsetHeight + gridRefs[i].current.children[1].offsetHeight )* 1.011
                <pageRefs[i-1].current.offsetHeight){
                        if (numberOfPages > 1 && pages[i].itemsOnPage > 1
                            && (gridRefs[i-1].current.offsetHeight + gridRefs[i].current.children[1].offsetHeight )* 1.011
                            <pageRefs[i-1].current.offsetHeight) {


                            pages[i - 1].itemsOnPage++;
                            pages[i].itemsOnPage--;
                            this.setState({
                                ...this.state,
                                pages: pages,
                            });

                        }else if (numberOfPages > 1 && pages[i].itemsOnPage === 1
                            && gridRefs[i-1].current.offsetHeight * 1.01 + gridRefs[i].current.children[1].offsetHeight
                            <pageRefs[i-1].current.offsetHeight) {

                            pages[i - 1].itemsOnPage++;
                            pages[i].itemsOnPage--;
                            this.setState({
                                ...this.state,
                                pages: pages,
                                numberOfPages:numberOfPages-1
                            });
                        }
                    }
                }

            // }
        })

    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        let state = {...this.state};
        let gridRefs = [...this.gridRefs];
        let pageRefs = [...this.pageRefs];
        let {pages} = state;
        let {numberOfPages} = state;

        // console.log(prevProps);
        // console.log('prev');



        pages.slice(0, numberOfPages).map((item, i) => {
            if (gridRefs[i].current.offsetHeight * 1.01 > pageRefs[i].current.offsetHeight
                && numberOfPages !== 4) {
                this.setHeight();
            }
        })
    }

    getAge =(DOB)=> {
        let today = new Date();
        let birthDate = new Date(DOB);
        let age = today.getFullYear() - birthDate.getFullYear();
        let m = today.getMonth() - birthDate.getMonth();
        if (m < 0 || (m === 0 && today.getDate() < birthDate.getDate())) {
            age = age - 1;
        }

        return age;
    };

